* {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  box-sizing: border-box;
}

body {
  padding: 0 50px;
}

:root {
  --violet:#7541c8;
  --grey:#48556a;
  --blue:#19212e;
  --light-grey:#cfcfcf;
}

.test__cointaner {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 2rem;
}
@media screen and (max-width: 1024px) {
  .test__cointaner {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, 1fr);
  }
}
@media screen and (max-width: 768px) {
  .test__cointaner {
    grid-template-columns: auto;
    grid-template-rows: auto;
  }
}

.test__item {
  padding: 1.25rem;
  border-radius: 3%;
  padding: 1.25rem;
}
.test__item__title {
  font-size: 1.5625rem;
}
.test__item__desc {
  font-size: 1.25rem;
}
.test__item--violet {
  grid-column: 1/span 2;
  grid-row: 1/1;
  grid-column: 1/span 2;
  background: var(--violet);
  color: white;
}
@media screen and (max-width: 1024px) {
  .test__item--violet {
    grid-row: auto/auto;
    grid-column: 1/3;
    background: var(--violet);
    color: white;
  }
}
@media screen and (max-width: 768px) {
  .test__item--violet {
    grid-column: auto;
    grid-row: auto;
  }
}
.test__item--grey {
  grid-row: 1/1;
  grid-column: 3/3;
  background: var(--grey);
  color: white;
}
@media screen and (max-width: 1024px) {
  .test__item--grey {
    grid-row: auto/auto;
    grid-column: 1/2;
    background: var(--grey);
    color: white;
  }
}
@media screen and (max-width: 768px) {
  .test__item--grey {
    grid-column: auto;
    grid-row: auto;
  }
}
.test__item--blue {
  grid-row: 2/2;
  grid-column: 2/span 2;
  background: var(--blue);
  color: white;
}
@media screen and (max-width: 1024px) {
  .test__item--blue {
    grid-row: auto/auto;
    grid-column: 1/2;
    background: var(--blue);
    color: white;
  }
}
@media screen and (max-width: 768px) {
  .test__item--blue {
    grid-column: auto;
    grid-row: auto;
  }
}
.test__item--white {
  grid-row: 2/2;
  grid-column: 1/1;
  background: var(--light-grey);
  color: black;
}
@media screen and (max-width: 1024px) {
  .test__item--white {
    grid-row: auto/auto;
    grid-column: 2/3;
    background: var(--light-grey);
    color: black;
  }
}
@media screen and (max-width: 768px) {
  .test__item--white {
    grid-column: auto;
    grid-row: auto;
  }
}
.test__item--white--long {
  grid-row: 1/span 2;
  grid-column: 4/4;
  background: var(--light-grey);
  color: black;
}
@media screen and (max-width: 1024px) and (max-width: 1024px) {
  .test__item--white--long {
    grid-row: auto/auto;
    grid-column: 2/3;
    background: var(--light-grey);
    color: black;
  }
}
@media screen and (max-width: 1024px) and (max-width: 768px) {
  .test__item--white--long {
    grid-column: auto;
    grid-row: auto;
  }
}
@media screen and (max-width: 768px) {
  .test__item--white--long {
    grid-column: auto;
    grid-row: auto;
  }
}

.challenge {
  text-align: center;
}

.profile__head {
  display: flex;
  align-items: center;
}
.profile__head img {
  border: 2px solid white;
  border-radius: 50%;
}
.profile__head .profile__head__name {
  margin-left: 1.125rem;
}
.profile__head .profile__head__name h2 {
  font-size: 0.9375rem;
}
.profile__head .profile__head__name p {
  font-size: 0.8125rem;
}